import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: () => import('../components/Index'),
      redirect: {
        name: 'Home'
      },
      children:[
        {
        path: 'home',
        name: 'Home',
        component: () => import('../views/Home')
        },
        {
          path: 'faq',
          name: 'FAQ',
          component: () => import('../views/FAQ'),
          redirect:{
            name: 'Flow'
          },
          children:[
            {
              path: 'flow',
              name: 'Flow',
              component: () => import('../views/AboutFAQ/Flow')
            },
            {
              path: 'deliver',
              name: 'Deliver',
              component: () => import('../views/AboutFAQ/Deliver')
            },
            {
              path: 'written',
              name: 'Written',
              component: () => import('../views/AboutFAQ/Written')
            },
            {
              path: 'interview',
              name: 'InterView',
              component: () => import('../views/AboutFAQ/InterView')
            },
            {
              path: 'internship',
              name: 'InternShip',
              component: () => import('../views/AboutFAQ/InternShip')
            }
          ]
        }
      ]
    }
  ]
})



